import { Platform } from '@ionic/angular';
import { AngularFirestore } from '@angular/fire/firestore';
import { Component, OnInit } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { Router, ActivatedRoute, NavigationExtras } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import * as L from 'leaflet'


import "leaflet/dist/images/marker-shadow.png";
import "leaflet/dist/images/marker-icon.png";
import "leaflet/dist/images/marker-icon-2x.png";

@Component({
  selector: 'app-slider',
  templateUrl: './slider.page.html',
  styleUrls: ['./slider.page.scss'],
})
export class SliderPage implements OnInit {
  lat;
  lng;
  zoomLevel

  constructor(private geo: Geolocation,private Afs: AngularFirestore,public platform:Platform) { 
    this.platform.ready().then(()=>{
      this.rangeVal = 12;
    })
  }
  map: L.Map
  homeMarker
  rangeVal=14;
  ngOnInit() {
  }
  
  zoom(){
    setInterval(function(){
      this.map.setZoom(0);
      setTimeout(function(){
          this.map.setZoom(1);
      }, 2000);
  }, 4000);
  }

  Iamhere() {
    this.geo.getCurrentPosition({ timeout: 10000, enableHighAccuracy: true }).then((res) => {
      this.lat = res.coords.latitude;
      this.lng = res.coords.longitude;
      this.createMap()
    }).catch((e) => {
      console.log(e)
    });
  }

  createMap() {
    this.zoomLevel = this.rangeVal;
    this.map = L.map('mapa', {
      center: [this.lat, this.lng],
      zoom: this.zoomLevel,
      renderer: L.canvas()
    })

    L.tileLayer('//{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: 'donn&eacute;es &copy; <a href="//osm.org/copyright">OpenStreetMap</a>/ODbL - rendu <a href="//openstreetmap.fr">OSM France</a>',
      minZoom: 1,
      maxZoom: 20
    }).addTo(this.map);

    this.addHomeMarker()



    // force map to resize on init
    setTimeout(() => {
      this.map.invalidateSize();
    }, 0);
  }

  addHomeMarker() {
    this.homeMarker = L.marker({ lat: this.lat, lng: this.lng }, { draggable: true })

    this.homeMarker.addTo(this.map)
  
  }
 

}
